'use client';

import { Canvas, useFrame } from '@react-three/fiber';
import { Float, Text, Sphere, Torus, Icosahedron, Cone } from '@react-three/drei';
import { useRef, useMemo } from 'react';
import * as THREE from 'three';

function MathSymbols() {
    return (
        <group>
            <Float speed={2} rotationIntensity={1} floatIntensity={1} position={[-5, 2, -10]}>
                <Torus args={[0.5, 0.2, 16, 32]} rotation={[Math.PI / 4, Math.PI / 4, 0]}>
                    <meshStandardMaterial color="#3b82f6" transparent opacity={0.3} />
                </Torus>
            </Float>
            <Float speed={1.5} rotationIntensity={1.5} floatIntensity={1} position={[6, -3, -15]}>
                <Icosahedron args={[0.8, 0]}>
                    <meshStandardMaterial color="#8b5cf6" transparent opacity={0.3} wireframe />
                </Icosahedron>
            </Float>
            <Float speed={2.5} rotationIntensity={0.5} floatIntensity={1.5} position={[-7, -4, -12]}>
                <Cone args={[0.5, 1, 32]} rotation={[0, 0, Math.PI / 6]}>
                    <meshStandardMaterial color="#10b981" transparent opacity={0.3} />
                </Cone>
            </Float>
        </group>
    );
}

function Atom() {
    const electrons = useMemo(() => {
        return Array.from({ length: 3 }).map((_, i) => ({
            radius: 1.5,
            speed: 2 + i * 0.5,
            offset: i * ((Math.PI * 2) / 3),
            axis: new THREE.Vector3(Math.random(), Math.random(), Math.random()).normalize(),
        }));
    }, []);

    return (
        <group position={[5, 3, -12]}>
            <Float speed={1} rotationIntensity={0.5} floatIntensity={0.5}>
                {/* Nucleus */}
                <Sphere args={[0.3, 32, 32]}>
                    <meshStandardMaterial color="#ef4444" transparent opacity={0.6} />
                </Sphere>

                {/* Electrons */}
                {electrons.map((electron, i) => (
                    <Electron key={i} {...electron} />
                ))}
            </Float>
        </group>
    );
}

function Electron({ radius, speed, offset, axis }: { radius: number; speed: number; offset: number; axis: THREE.Vector3 }) {
    const ref = useRef<THREE.Mesh>(null);

    useFrame(({ clock }) => {
        if (ref.current) {
            const time = clock.getElapsedTime() * speed + offset;
            // Simple orbit logic
            const x = Math.cos(time) * radius;
            const z = Math.sin(time) * radius;

            // Apply rotation based on axis (simplified for visual effect)
            ref.current.position.set(x, 0, z).applyAxisAngle(axis, Math.PI / 4);
        }
    });

    return (
        <Sphere ref={ref} args={[0.08, 16, 16]}>
            <meshStandardMaterial color="#3b82f6" transparent opacity={0.5} />
        </Sphere>
    );
}

function Molecule() {
    return (
        <group position={[0, 0, -20]}>
            <Float speed={1.2} rotationIntensity={0.8} floatIntensity={0.8}>
                <group rotation={[Math.PI / 6, Math.PI / 3, 0]}>
                    {/* Central Atom */}
                    <Sphere args={[0.4, 32, 32]} position={[0, 0, 0]}>
                        <meshStandardMaterial color="#f59e0b" transparent opacity={0.5} />
                    </Sphere>

                    {/* Bonded Atoms */}
                    <Sphere args={[0.25, 32, 32]} position={[0.8, 0.5, 0]}>
                        <meshStandardMaterial color="#06b6d4" transparent opacity={0.5} />
                    </Sphere>
                    <Sphere args={[0.25, 32, 32]} position={[-0.8, 0.5, 0]}>
                        <meshStandardMaterial color="#06b6d4" transparent opacity={0.5} />
                    </Sphere>

                    {/* Bonds */}
                    <mesh position={[0.4, 0.25, 0]} rotation={[0, 0, Math.PI / 3]}>
                        <cylinderGeometry args={[0.05, 0.05, 1, 8]} />
                        <meshStandardMaterial color="#94a3b8" transparent opacity={0.3} />
                    </mesh>
                    <mesh position={[-0.4, 0.25, 0]} rotation={[0, 0, -Math.PI / 3]}>
                        <cylinderGeometry args={[0.05, 0.05, 1, 8]} />
                        <meshStandardMaterial color="#94a3b8" transparent opacity={0.3} />
                    </mesh>
                </group>
            </Float>
        </group>
    );
}

export default function Background3D() {
    return (
        <div className="fixed inset-0 z-[-1] pointer-events-none overflow-hidden">
            <Canvas camera={{ position: [0, 0, 5], fov: 50 }}>
                <ambientLight intensity={0.5} />
                <pointLight position={[10, 10, 10]} intensity={1} />
                <MathSymbols />
                <Atom />
                <Molecule />
            </Canvas>
        </div>
    );
}
